@import '../../partials/colours';
@import '../../partials/mixins';

.great-domestic-international-links {
  a {
    color: $mid-grey;
  }

  .active {
    color: $great-blue;

    &:after {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 4px;
      background-color: $flag-red;
      content: '';
    }
  }
}

@media (max-width: 768px) {
  .great-global-header {
    @include float-clear;
    height: auto;

    &.signed-in {
      display: none;
    }
  }

  #great-global-header-domestic-link {
    text-align: left;
    padding-left: 15px;
  }
}
